<template>
  <v-text-field
    v-model="description"
    label="Create A New Task Here..."
    solo
    clearable
    @keydown.enter="onEnter"
    class="mb-12"
  ></v-text-field>
</template>

<script>
import { mapActions } from "vuex";

export default {
  methods: {
    ...mapActions(["addTask"]),
    onEnter() {
      this.addTask({ description: this.description });
      this.description = "";
    },
  },
  data() {
    return {
      description: "",
    };
  },
};
</script>

<style lang="scss" scoped>
.mb-12 {
  margin-bottom: 16px !important;
}
</style>
