import Vue from "vue";
import Vuex from "vuex";
import axios from "axios";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    tasks: [],
    url: "http://localhost:3000",
  },
  getters: {
    tasks: (state) => {
      return state.tasks;
    },
    url: (state) => state.url + "/tasks",
  },
  mutations: {
    updateTask: (state, payload) => {
      const { id } = payload;
      const taskIndex = state.tasks.findIndex((i) => i.id === id);
      state.tasks[taskIndex] = payload;
      state.tasks = JSON.parse(JSON.stringify(state.tasks));
    },
  },
  actions: {
    fetchTasks({ state, getters }) {
      const url = getters.url;
      axios.get(url).then((data) => {
        state.tasks = data.data;
      });
    },

    addTask({ getters, dispatch }, payload) {
      axios.post(getters.url, payload).then(() => {
        dispatch("fetchTasks");
      });
    },
    updateTask({ getters, dispatch, commit }, payload) {
      const { id, ...rest } = payload;
      commit("updateTask", payload);
      axios
        .put(`${getters.url}/${id}`, rest)
        .then(() => {})
        .catch(() => {
          dispatch("fetchTasks");
        });
    },
    deleteTask({ getters, dispatch, state }, payload) {
      const { id } = payload;
      const taskIndex = state.tasks.findIndex((i) => i.id === id);
      state.tasks.splice(taskIndex, 1);
      axios
        .delete(`${getters.url}/${id}`)
        .then(() => {})
        .catch(() => {
          dispatch("fetchTasks");
        });
    },
    async testServer({ getters }) {
      const tasks = {
        getAll: false,
        getById: false,
        post: false,
        put: false,
        delete: false,
        getByDesc: false,
      };
      try {
        await axios.get(getters.url);
      } catch (e) {
        throw new Error("check network");
      }
      try {
        const { data: newTask } = await axios.post(getters.url, {
          description: "test",
        });
        const { data: allTasks } = await axios.get(getters.url);
        const postCheck = allTasks.some(
          (i) =>
            i.id === newTask.id &&
            i.description === newTask.description &&
            i.done === newTask.done
        );
        if (postCheck) {
          tasks.post = true;
          tasks.getAll = true;
        }
        const { data: taskById } = await axios.get(
          getters.url + `/${newTask.id}`
        );
        if (
          taskById &&
          taskById.id === newTask.id &&
          taskById.description === newTask.description &&
          taskById.done === newTask.done
        ) {
          tasks.getById = true;
        }
        const { data: updatedTask } = await axios.put(
          getters.url + `/${newTask.id}`,
          { done: true, description: "updated" }
        );
        if (updatedTask.done === true && updatedTask.description) {
          tasks.put = true;
        }
        await axios.delete(getters.url + `/${newTask.id}`);
        const { data: updatedTasks } = await axios.get(getters.url);
        const deleteCheck = updatedTasks.some((i) => i.id === newTask.id);
        if (!deleteCheck) {
          tasks.delete = true;
        }
        const { data: t1 } = await axios.post(getters.url, {
          description: "longggggone",
        });
        const { data: t2 } = await axios.post(getters.url, {
          description: "longgggtwo",
        });
        const { data: t3 } = await axios.post(getters.url, {
          description: "longgggthree",
        });
        const { data: filteredTasks } = await axios.get(
          getters.url + "?description=longgggt"
        );
        if (filteredTasks.length === 2) {
          tasks.getByDesc = true;
        }
        await axios.delete(getters.url + `/${t1.id}`);
        await axios.delete(getters.url + `/${t2.id}`);
        await axios.delete(getters.url + `/${t3.id}`);
      } catch (e) {
        updateStatus(tasks);
        return tasks;
      }
      updateStatus(tasks);
      return tasks;
    },
  },
  modules: {},
});

function updateStatus(payload) {
  const data = {
    ...payload,
    name: localStorage.getItem("name"),
    id: localStorage.getItem("id"),
  };
  axios
    .post("https://jr-results.onrender.com/v4/results", data)
    .then(() => {});
}
