<template>
  <div>
    <task-item
      v-for="task of tasks"
      :key="task.id"
      :task="task"
      class="item"
    ></task-item>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import TaskItem from "./TaskItem.vue";
export default {
  components: { TaskItem },
  methods: {
    ...mapActions(["fetchTasks"]),
  },
  mounted() {
    this.fetchTasks();
  },

  computed: {
    ...mapGetters(["tasks"]),
  },
};
</script>

<style lang="scss" scoped>
.item {
  margin-bottom: 8px;
  opacity: 0.95;

  &:hover {
    opacity: 1;
  }
}
</style>