<template>
  <v-card>
    <v-row>
      <v-checkbox
        v-model="task.done"
        hide-details
        class="shrink mt-6 checkbox"
        @click="onClick"
      ></v-checkbox>
      <v-text-field
        v-model="description"
        class="text"
        :class="{ done: task.done }"
        @blur="onBlur"
        @keyup.enter="onEnter($event)"
      ></v-text-field>
      <v-btn class="delete-btn mt-6" @click="onDelete" depressed>
        <v-icon> mdi-delete </v-icon>
      </v-btn>
    </v-row>
  </v-card>
</template>

<script>
import { mapActions } from "vuex";
export default {
  props: ["task"],
  data() {
    return {
      edit: false,
      description: "",
    };
  },
  methods: {
    ...mapActions(["updateTask", "deleteTask"]),
    onClick() {
      this.updateTask(this.task);
    },
    onDelete() {
      this.deleteTask(this.task);
    },
    onBlur() {
      this.description = this.task.description;
    },
    onEnter(event) {
      event.target.blur();
      this.updateTask({ ...this.task, description: this.description });
    },
  },
  watch: {
    task: {
      immediate: true,
      handler(task) {
        this.description = task.description;
      },
    },
  },
};
</script>

