.row {
  margin: 0;
  align-items: center;

  .v-input {
    margin: 0 8px;
  }

  .text::v-deep .v-input__slot {
    margin: 0;

    &::before {
      border: none;
    }
  }

  .done {
    text-decoration: line-through;
  }

  button.delete-btn {
    color: #d62828;
    background-color: transparent;
    margin-right: 8px;
    min-width: 10px;
    padding: 0 8px;
    opacity: 0;
  }

  &:hover {
    button.delete-btn {
      opacity: 1;
    }
  }

  .checkbox {
    color: #43aa8b;
    &::v-deep {
      .mdi-checkbox-marked::before {
        color: #43aa8b;
