<template>
  <div class="bg">
    <div class="modal">
      <div class="header">
        <h4>Run Tests</h4>
        <span @click="close" class="close-btn"> &#10005; </span>
      </div>
      <div class="content" v-if="nameExist">
        <div class="control">
          <v-progress-linear
            color="deep-purple accent-4"
            indeterminate
            rounded
            height="6"
            v-if="processing"
          ></v-progress-linear>
          <v-btn color="success" outlined @click="startTest" v-else
            >Start</v-btn
          >
        </div>

        <div class="tasks">
          <p :class="{ success: tasks.post }">POST /tasks</p>
          <p :class="{ success: tasks.getAll }">GET /tasks</p>
          <p :class="{ success: tasks.getById }">GET /tasks/:id</p>
          <p :class="{ success: tasks.put }">PUT /tasks/:id</p>
          <p :class="{ success: tasks.delete }">DELETE /tasks</p>
          <p :class="{ success: tasks.getByDesc }">
            GET /tasks?description=xxx
          </p>
        </div>
        <p class="error-msg" v-if="errorMsg">{{ errorMsg }}</p>
      </div>
      <div class="content center" v-else>
        <v-text-field
          v-model="name"
          label="Your name"
          outlined
          :rules="rules"
          @keydown.enter="updateName"
        ></v-text-field>
        <v-btn outlined color="success" @click="updateName">Enter</v-btn>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions } from "vuex";
import { v4 as uuidv4 } from "uuid";
export default {
  data() {
    return {
      processing: false,
      tasks: {
        getAll: false,
        getById: false,
        post: false,
        put: false,
        delete: false,
        getByDesc: false,
      },
      errorMsg: "",
      name: "",
      nameExist: false,
      rules: [
        (value) => !!(value || "").trim() || "Required.",
        (value) => (value || "").length >= 2 || "Min 2 characters",
      ],
    };
  },
  mounted() {
    const name = localStorage.getItem("name");
    if (name) {
      this.nameExist = true;
    }
    const id = localStorage.getItem("id");
    if (!id) {
      localStorage.setItem("id", uuidv4());
    }
  },
  methods: {
    ...mapActions(["testServer"]),
    close() {
      this.$emit("onClose");
    },
    updateName() {
      if (this.name.trim().length < 2) {
        return;
      }
      localStorage.setItem("name", this.name);
      this.nameExist = true;
    },
    startTest() {
      this.tasks = {};
      this.errorMsg = "";
      this.processing = true;
      this.testServer()
        .then((res) => {
          this.tasks = res;
          this.processing = false;
        })
        .catch(() => {
          this.errorMsg =
            "Cannot connect to your server, check console for more details. (make sure it's listening on http://localhost:3000)";
          this.processing = false;
        });
    },
  },
};
</script>

